body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  margin:0;
  padding:0;
}

html {
  height: 100%;
}

body {
   min-height: 100%;
  position:relative;
  background: url(/weatherApp/static/media/bg-app.8b5b3ba9.jpg) no-repeat center top transparent;
  background-attachment: fixed;
  background-size: cover;
}

.App {
  text-align: center;
}

input, button{
  margin: 20px;
  padding: 10px 30px;
}

.header {
  position: relative;
  top: 0;
  padding: 30px 0;
}

.footer {
  position:absolute;
  bottom:0;
  width:100%;
  height:60px;		
}


